.product-shop-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.product-upper-container {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding: 1rem;
}

.product-shop {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-top: 1rem;
    padding-bottom: 5rem;
}

