.pagination-container {
    display: flex;
    justify-content: center;
    margin: 3rem auto; /* More standardized spacing */
    padding: 1rem; /* Consistent internal padding */
    max-width: 100%; /* Avoid overflow */
    box-sizing: border-box; /* Include padding in the element's total width */
}
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none; /* Remove default list styling */
    padding: 0;
    margin: 0;
    gap: 0.5rem; /* Space out pagination items */
}
.pagination li {
    margin: 0; /* Ensure no default margin */
}
.pagination a {
    text-decoration: none;
    color: #333; /* More neutral, professional color */
    padding: 0.8rem 1.2rem;
    border-radius: 4px;
    transition: background-color 0.3s ease, color 0.3s ease;
}
.page-item {
    --bs-pagination-bg: transparent;
    --bs-pagination-color: white;
    --bs-pagination-disabled-bg: transparent;
    --bs-pagination-disabled-color: white;
    --bs-pagination-hover-bg: aqua;
    cursor: pointer;
    transition: all ease-in-out 0.3s;
    margin: 0;
    background-color: transparent;
    color: #fff;
}

.page-link {
    background: transparent;
    color: #fff;
}
.pagination a:hover,
.pagination a:focus {
    background-color: #007bff; /* Primary button color */
    color: #fff; /* Text color changes on hover */
}
.pagination .active a {
    background-color: #28a745; /* Highlight active item */
    color: #fff; /* Ensure active text stands out */
}

.pagination .disabled a {
    background-color: #f8f9fa; /* Lighter color for disabled items */
    color: #ddd; /* Disabled text color */
    pointer-events: none; /* Disable click */
}

.page-link:hover {
    background: aqua;
    transition: all ease-in-out 0.5s;
    color: #fff;
    border: 1px solid aqua;
}

.pageLinkActive {
    background-color: aqua;
    color: #000;
    border: 1px solid aqua;
}

.page-item li {
    margin-right: 0;
}

.drawer-icon {
    width: 1rem;
    height: 1rem;
    margin-right: 0.5rem;
}

.price-range-container {
    display: flex;
    flex-direction: column;
}

.price-range-image {
    width: 2rem;
    height: 2rem;
}

/* Responsive styles */
@media (max-width: 1200px) {
    .product-shop-container {
        width: 100%;
    }

    .product-shop {
        flex-direction: column;
        align-items: center;
    }
}

@media (max-width: 768px) {
    .product-shop {
        padding: 0 1rem;
    }

    .pagination-container {
        margin: 2vmax;
    }

    .page-item {
        font-size: 0.8rem;
    }

    .drawer-icon {
        width: 1.5rem;
        height: 1.5rem;
    }
}

@media (max-width: 480px) {
    .product-shop {
        padding: 0 0.5rem;
    }

    .product-card {
        width: 100%;
        margin: 0.5rem 0;
    }

    .pagination-container {
        margin: 1vmax;
    }

    .page-item {
        font-size: 0.6rem;
    }

    .drawer-icon {
        width: 2rem;
        height: 2rem;
    }
}
